import { Table, TableColumn } from 'element-ui'
import RouteBreadCrumb from '@/components/argon-core/Breadcrumb/RouteBreadcrumb'
import MembersCheckin from '~/components/shack/MembersCheckin.vue'
import MainTitle from '~/components/shack/MainTitle.vue'
import SectionTitle from '~/components/shack/SectionTitle.vue'
import SearchForm from '~/components/shack/SearchForm.vue'
import { Select, Option } from 'element-ui'
import { displayError } from '../../../util/errors'
import { mapState } from 'vuex'

const moment = require('moment-timezone')

export default {
  name: 'Checkin',
  layout: 'ShackDash',
  components: {
    MainTitle,
    SearchForm,
    SectionTitle,
    MembersCheckin,
    RouteBreadCrumb,
    [Select.name]: Select,
    [Option.name]: Option,
    QrcodeStream: () => import('@/components/qrscanner/qrscanner.vue')
  },
  async asyncData({ $membership, $checkin, error, route }) {
    try {
      const link = 'filter[status]=accepted&include=profile'
      const checkinFilter = `?filter[status]=checkin&filter[checkin_timestamp]=${moment().tz("America/Los_Angeles").format(
        'YYYY-MM-DD'
      )},${moment()
        .add(1, 'days')
        .tz("America/Los_Angeles")
        .format('YYYY-MM-DD')}&page=${route.query.page}&sort=-id`

      let imeta, ilinks

      console.log(checkinFilter)

      const checkins = await $checkin
        .checkins(checkinFilter)
        .then(({ data, meta, links }) => {
          imeta = meta
          ilinks = links
          return data
        })

      return await $membership.getAllMemberships(link).then(({ data }) => {
        return { data, checkins, meta: imeta, links: ilinks }
      })
    } catch (e) {
      const message = e.response ? e.response.data.message : e.message

      error({ statusCode: e.statusCode, message: message })
    }
  },
  data: () => ({
    searching: false,
    type: 0,
    first_name: '',
    last_name: '',
    email: '',
    company: '',
    email: '',
    membership_id: '',
    booking_id: '',
    booking_info: '',
    loading: false,
    scanComplete: false,
    meeting_guest: false,
    options: [
      {
        value: 'Option1',
        label: 'Option1'
      },
      {
        value: 'Option2',
        label: 'Option2'
      },
      {
        value: 'Option3',
        label: 'Option3'
      },
      {
        value: 'Option4',
        label: 'Option4'
      },
      {
        value: 'Option5',
        label: 'Option5'
      }
    ],
    value: ''
  }),
  computed: {
    ...mapState({
      bookings: state => state.resources.bookingsForDate
    })
  },
  mounted() {
    const data = {
      from: this.$moment().tz("America/Los_Angeles").format('YYYY-MM-DD'),
      to: this.$moment()
        .add(1, 'days')
        .tz("America/Los_Angeles")
        .format('YYYY-MM-DD')
    }
    this.$store.dispatch('resources/getBookingsByDate', data)
  },
  methods: {
    onDecode(data) {
      if (data) {
        this.scanComplete = true
        this.membership_id = data.split('//')[1]
        this.checkinMember('scan')
      }
    },
    next() {
      const { next } = this.links
      const params = getQueryParams(next)

      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    prev() {
      const { prev } = this.links
      const params = getQueryParams(prev)

      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    changePage(pageNumber) {
      const params = `?page=${pageNumber}`
      this.$router.push(params)
      location.href = location.origin + this.$route.path + params
    },
    searchMembers(query) {
      const link = `filter[search]=${query}&filter[status]=accepted`

      this.searching = !this.searching

      const _self = this

      _.debounce(() => {
        _self.$membership.getAllMemberships(link).then(({ data }) => {
          _self.searching = !_self.searching
          _self.data = data
        })
      }, 350)()
    },
    checkinGuest() {
      this.loading = !this.loading
      this.booking_id = this.booking_info.split(' ')[0]
      this.membership_id = this.membership_id
        ? this.membership_id
        : this.booking_info.split(' ')[1]
      const {
        first_name,
        last_name,
        email,
        company,
        membership_id,
        booking_id,
        meeting_guest
      } = this
      this.$checkin
        .checkin({
          type: 'guest',
          first_name,
          last_name,
          email,
          company,
          membership_id,
          booking_id,
          meeting_guest
        })
        .then(({ data }) => {
          this.loadiing = !this.loading
          // this.checkins.push(data)
          this.$bvToast.toast(`Guest checked in successfully`, {
            title: 'Success',
            variant: 'success'
          })

          this.$bvModal.hide('checkin')
          location.reload()
        })
        .catch(e => {
          this.loading = !this.loading
          const message = e.response ? e.response.data.message : e.message
          // console.log(e.message)
          // this.$bvToast.toast(e.message, { title: 'Error', variant: 'danger' })

          this.$bvToast.toast(`Error - ${message}`, {
            title: 'Error',
            variant: 'danger'
          })
        })
    },
    checkinMember(source = 'manual') {
      this.loading = !this.loading
      const { membership_id } = this
      try {
        this.$checkin
          .checkin({
            type: 'member',
            checkin_type: source,
            membership_id,
            membership: membership_id
          })
          .then(({ data }) => {
            this.loading = false
            // this.checkins.push(data)

            this.$bvToast.toast('Member checked in successfully', {
              title: 'Success',
              variant: 'success'
            })

            this.$bvModal.hide('scanqrcode')

            this.$bvModal.hide('checkin')
            location.reload()
          })
          .catch(error => {
            this.$bvModal.hide('scanqrcode')

            this.loading = !this.loading
            const message = error.response
              ? error.response.data.message
              : error.message
            this.$bvToast.toast(message, { title: 'Error', variant: 'danger' })
          })
      } catch (e) {
        this.loading = !this.loading
        const message = e.response ? e.response.data.message : e.message
        this.$bvModal.hide('scanqrcode')
        this.$bvToast.toast(message, { title: 'Error', variant: 'danger' })
      }
    },
    togggleLoading() {
      this.loading = !this.loading
    },
    toggleModal(id) {
      this.$bvModal.show(id)
      this.scanComplete = false
    }
  }
}
